.about{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10%;
}

.about-title{
    font-size: 35px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin-bottom: 50px;
}

.about-row{
    display:flex;
}

.about-description{
    width: 50%;
    letter-spacing: 2px;
    word-wrap: break-word;
    font-size: 16px;
    font-family: Arial;
    color: #282c34;
}
.about-pic{
    width:50%;
}
.about-pic-user-responsive{
    max-width:100%;
    max-height:100%;
    width: auto;
    height: auto;
}

.about-description-h4{
    font-size: 30px;
    font-family: Consolas, Menlo, Courier, monospace;
    color: blueviolet;
}

.customer{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
    padding-right: 10%;
    padding-left: 10%;
}

.customer-title{
    font-size: 35px;
    font-weight: 700;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin-bottom: 50px;
}

.customer-pics-list-row{
    display:flex;
    margin-bottom: 50px;
}


.customer-pics-list-row1{
    display:flex;

}

.customer-pics-list-row1-title{
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 1px;
    margin-bottom: 8px;
    text-transform: capitalize;
}

.customer-pics-list-row1-description{
    font-size: 14px;
    line-height: 24px;
    color:#555;
}

.cust-img{
    max-width: 100px;
    transition: all 0.5s ease-out 0s;
}
.cust-img:hover{
    transform: scale(1.15);
}
.cust-img-a{
    border: 1px solid #e5e5e5;
    display:block;
    overflow: hidden;
    padding:10px;
}

.about-info{
    padding: 0 30px;
    vertical-align: top;
}