body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.about{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10%;
}

.about-title{
    font-size: 35px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin-bottom: 50px;
}

.about-row{
    display:flex;
}

.about-description{
    width: 50%;
    letter-spacing: 2px;
    word-wrap: break-word;
    font-size: 16px;
    font-family: Arial;
    color: #282c34;
}
.about-pic{
    width:50%;
}
.about-pic-user-responsive{
    max-width:100%;
    max-height:100%;
    width: auto;
    height: auto;
}

.about-description-h4{
    font-size: 30px;
    font-family: Consolas, Menlo, Courier, monospace;
    color: blueviolet;
}

.customer{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 50px;
    padding-right: 10%;
    padding-left: 10%;
}

.customer-title{
    font-size: 35px;
    font-weight: 700;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 4px;
    margin-bottom: 50px;
}

.customer-pics-list-row{
    display:flex;
    margin-bottom: 50px;
}


.customer-pics-list-row1{
    display:flex;

}

.customer-pics-list-row1-title{
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 1px;
    margin-bottom: 8px;
    text-transform: capitalize;
}

.customer-pics-list-row1-description{
    font-size: 14px;
    line-height: 24px;
    color:#555;
}

.cust-img{
    max-width: 100px;
    transition: all 0.5s ease-out 0s;
}
.cust-img:hover{
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
}
.cust-img-a{
    border: 1px solid #e5e5e5;
    display:block;
    overflow: hidden;
    padding:10px;
}

.about-info{
    padding: 0 30px;
    vertical-align: top;
}
.gallery{
    display: flex;
    margin-top: 5%;
    flex-direction: column;
    align-items: center;
    padding: 10%;
}

.gallery-title{
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 4px;
    color: #000;
    margin-bottom: 60px;
}

.gallery-pictures-list{
    display: flex;
    width:80px;
    height: 80px;
    border: 1px solid #000;
    flex-wrap: wrap;
}

.gallery-pictures-list div{
    border: 1px solid #e5e5e5;
}
.logo {
    width: 120px;
    height: 31px;
    color:#ffffff;
    text-align: center;
    font-size: 26px;
    font-weight: 700;
    float: left;
  }

  .banner{
    background: url(../../static/media/banner.4f0a7ea3.png);
    height: 480px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
  }

  .banner-title{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .banner-title-h1{
      letter-spacing: 4px;
      font-weight: 700;
      line-height: 1.3;
      text-transform: uppercase;
      font-size: 40px;
      color:#fff;
      font-family: Oswald;
      margin-bottom: 50px;
  }
  .banner-title-button{
      text-transform: uppercase;
      letter-spacing: 4px;
  }

  .affix{
    float: right;
    margin-right: 5%;
    margin-top: -36%;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

