.logo {
    width: 120px;
    height: 31px;
    color:#ffffff;
    text-align: center;
    font-size: 26px;
    font-weight: 700;
    float: left;
  }

  .banner{
    background: url(../../images/banner.png);
    height: 480px;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
  }

  .banner-title{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .banner-title-h1{
      letter-spacing: 4px;
      font-weight: 700;
      line-height: 1.3;
      text-transform: uppercase;
      font-size: 40px;
      color:#fff;
      font-family: Oswald;
      margin-bottom: 50px;
  }
  .banner-title-button{
      text-transform: uppercase;
      letter-spacing: 4px;
  }

  .affix{
    float: right;
    margin-right: 5%;
    margin-top: -36%;
}