.gallery{
    display: flex;
    margin-top: 5%;
    flex-direction: column;
    align-items: center;
    padding: 10%;
}

.gallery-title{
    font-size: 30px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 4px;
    color: #000;
    margin-bottom: 60px;
}

.gallery-pictures-list{
    display: flex;
    width:80px;
    height: 80px;
    border: 1px solid #000;
    flex-wrap: wrap;
}

.gallery-pictures-list div{
    border: 1px solid #e5e5e5;
}